      }
    }
  }

  @include breakpoint($screen-xlarge) {
    .project-list {
      padding: 3.5% 0;
    }

    .project-list-item {
      width: 23.5%;

      &:nth-child(7n+1) {
        left: 12.75%;
        margin-top: -4%;
      }

      &:nth-child(7n+2) {
        left: 38.25%;
